.error {
	@extend %d-block;

	position: relative;
	height: 100vh;
	overflow: hidden;

	&__block {
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: url(/images/standard/bg.png);

		&--1 {
			height: 62%;
			background-color: hsl(288, 14%, 93%);

			@include media-breakpoint-up(md) {
				height: 65%;
			}

			@include media-breakpoint-up(lg) {
				height: 55%;
			}
		}

		&--2 {
			height: 55%;
			background-color: hsl(270, 34%, 87%);

			@include media-breakpoint-up(md) {
				height: 50%;
			}

			@include media-breakpoint-up(xl) {
				height: 40%;
			}
		}

		&--3 {
			z-index: 8;
			height: 40%;
			background-color: hsl(270, 31%, 84%);

			@include media-breakpoint-up(md) {
				height: 30%;
			}

			@include media-breakpoint-up(xl) {
				height: 25%;
			}
		}
	}

	&__404 {
		position: absolute;
		z-index: 5;
		bottom: 75%;
		width: 100%;
		background-clip: text;
		background-color: $text-color;
		background-image: url(/images/standard/bg.png);
		color: $text-color;
		font-size: 140px;
		font-weight: 500;
		line-height: 1;
		text-align: center;
		-webkit-text-fill-color: transparent;

		@include media-breakpoint-up(md) {
			font-size: 250px;
		}

		@include media-breakpoint-up(lg) {
			font-size: 400px;
		}

		@include media-breakpoint-up(xl) {
			font-size: 500px;
		}
	}

	&__logo {
		position: relative;
		z-index: 10;
		padding-top: 55px;
		text-align: center;

		@include media-breakpoint-up(md) {
			padding-top: 60px;
		}

		@media ( max-height: 600px ) {
			padding-top: 20px;
		}

		img {
			width: 110px;
			height: 72px;

			@include media-breakpoint-up(md) {
				width: 200px;
				height: 131px;
			}
		}
	}

	&__info {
		padding-top: 50px;
		text-align: center;

		@include media-breakpoint-up(sm) {
			padding-top: 0;
		}

		@media ( max-height: 600px ) {
			padding-top: 0;
		}
	}

	.headline {
		margin-bottom: 40px;
		font: {
			size: 26px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 60px;
			font: {
				size: 40px;
			}
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 80px;
			font: {
				size: 40px;
			}
		}

		@media ( max-height: 600px ) {
			margin-bottom: 20px;
		}
	}
}
